import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Index from '@/view/Index'
import MyInfo from '@/view/my/MyInfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/my/info',
      name: 'MyInfo',
      component: MyInfo
    }
  ]
})



