<template>
  <div>
    <div class="menu-header">
      <div class="header-img">
        <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1601439115003&di=f9478d0ff55895c5f60b738266c80c48&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20171114%2F0fc43e9ad58f4a5cb41a018925b0e475.jpeg"/>
      </div>
      <div class="header-info">
        <p>
          <label>昵称:</label> <a>chuan</a> &nbsp; &nbsp;
          <label>入坑时长:</label> <a>3年</a>
        </p>
      </div>
    </div>
    <div class="menu-body">
      <a-list :data-source="menuData">
        <a-list-item slot="renderItem" slot-scope="item, index">
          {{ item }}
        </a-list-item>
      </a-list>
    </div>
    <div class="menu-footer">footer</div>
  </div>
</template>

<script>
export default {
  name: 'MenuBar',
  data () {
    return {
      menuData: ['home', 'tag', 'classification', 'about', 'other']
    }
  }
}
</script>

<style scoped>
.menu-header{
  width: 100%;
  text-align: center;
}
.header-img{
  width: 100px;
  height: 100px;
  margin: 40px auto;
  margin-bottom: 10px;
}
.header-img>img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: #1890ff solid 2px;
}
.header-info>p{
  font-size: 16px;
}
</style>



