<template>
  <div id="index-box">
    <div class="header">
      <div class="menu-box">
        <a-button type="dashed" :class="menuClassName"
                  @click="visible = !visible"> <a-icon type="menu" /> MENU</a-button>
      </div>
      <!-- 菜单栏 -->
      <a-drawer
        width="580"
        placement="right"
        :visible="visible"
        @close="visible = false">
        <MenuBar/>
      </a-drawer>
      <!-- 菜单栏 -->
    </div>

    <div class="content">
      <DocumentList/>
    </div>

    <div class="footer">
      <p>technology sharing&nbsp;<a-icon type="instagram" />&nbsp;Record Life</p>
      <p>blog creation time <a>{{ createTime }}</a></p>
      <p>Copyright © 2020 Developed by <a>Chuan</a> independently</p>
      <p>Currently running version information: v.1.0.0</p>
    </div>
  </div>
</template>

<script>
import MenuBar from '../components/menu/MenuBar'
import DocumentList from './document/DocumentList'
export default {
  name: 'Index',
  data () {
    return {
      visible: false,
      createTime: new Date(),
      menuClassName: 'menu-open-btn menu-open-btn-white'
    }
  },
  components: {
    DocumentList,
    MenuBar
  },
  mounted () {
    window.addEventListener('scroll', this.getScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.getScroll)
  },
  methods: {
    getScroll () {
      document.documentElement.scrollTop < 650
        ? this.menuClassName = 'menu-open-btn menu-open-btn-white'
        : this.menuClassName = 'menu-open-btn menu-open-btn-blue'
    }
  }
}
</script>

<style scoped>
html, body{
  padding: 0;
  margin: 0;
  height: 100%;
  min-height: 100%;
  font-family: Merriweather, "Open Sans", "Microsoft Jhenghei", "Microsoft Yahei", sans-serif;
}
.header{
  /*position: absolute;*/
  background: url("https://www.hai456.com/wordpress/project/program/static.runoob.com/images/demo/demo2.jpg") no-repeat;
  /*background: url("https://wimg.588ku.com/gif/20/04/16/42caef1c743042edcf9a6dccdef88969.gif") no-repeat;*/
  /*background: url("http://img95.699pic.com/photo/40143/6428.gif_wh860.gif") no-repeat;*/
  background-size: cover;
  width: 100%;
  height: 758px;
}
.menu-box{
  position: fixed;
  width: 100%;
  height: 100px;
  min-height: 100px;
  max-height: 100px;
  padding: 24px;
  top: 60px
}
.menu-open-btn{
  float: right;
  margin-right: 3%;
  font-size: 14px;
  background: none;
}
.menu-open-btn-blue{
  color: #1890ff;
  border: #1890ff solid 1px;
}
.menu-open-btn-white{
  color: #fff;
  border: #fff solid 1px;
}
.content{
  /*position: absolute;*/
  width: 100%;
  color: white;
  /*background: url("https://wimg.588ku.com/gif/20/04/16/42caef1c743042edcf9a6dccdef88969.gif") no-repeat;*/
  /*background-size: cover;*/
}
.footer{
  margin-top: 200px;
  text-align: center;
  padding: 8px 0;
}
</style>



